import { GET, POST,_POST } from "./net/net"

export const GetExamsAndClasses = async () => {
    return await GET({
        url: "/info/examAndClasses",
    })
}


export const ArrangeStudent = async (data) => {
    return await GET({
        url: "/arrange/student",
        params: data
    })
}


export const ArrangeFirst = async (data) => {
    return await GET({
        url: "/arrange/first",
        params: data
    })
}

export const Search = async (data) => {
    return await GET({
        url: "/info/exam/student",
        params: data
    })
}