<template>
  <div>
    <h1>上传信息</h1>
    <el-upload
      class="upload-demo"
      action="http://localhost:8082/import/classroom"
      multiple
      :limit="1"
    >
      <el-button size="small" type="primary">上传教室信息</el-button>
    </el-upload>
    <div style="margin: 30px"></div>
    <el-upload
      class="upload-demo"
      action="http://localhost:8082/import/student"
      multiple
      :limit="1"
    >
      <el-button size="small" type="primary"
        >上传学生、考场、老师信息</el-button
      >
    </el-upload>
    <el-divider></el-divider>
    <h1>安排考试</h1>
    <el-button
      type="primary"
      style="margin-right: 10px"
      v-loading.fullscreen.lock="fullscreenLoading"
      @click="arrangeFirst"
      >安排考场</el-button
    >
    <el-select v-model="arrangeType" placeholder="请安排座次">
      <el-option
        v-for="item in types"
        :key="item.id"
        :label="item.label"
        :value="item.value"
      >
      </el-option>
    </el-select>
    <el-divider></el-divider>
    <div style="margin: 30px"></div>
    <h1>查询</h1>
    <el-select v-model="examId" placeholder="请选择考试">
      <el-option
        v-for="item in exams"
        :key="item.id"
        :label="item.name"
        :value="item.id"
      >
      </el-option>
    </el-select>
    <span style="margin: 10px"></span>
    <el-select v-model="classNum" placeholder="请选择班级">
      <el-option
        v-for="item in selectedClasses"
        :key="item.classNum"
        :label="item.classNum"
        :value="item.classNum"
      >
      </el-option>
    </el-select>
    <el-button type="primary" style="margin-left: 10px" @click="search"
      >查询</el-button
    >

    <div style="margin: 30px"></div>
    <div flex v-if="tableData && tableData.length > 0">
      <el-table :data="tableData" border style="width: 100%">
        <el-table-column type="index" label="序号" width="50">
        </el-table-column>
        <el-table-column prop="name" label="姓名" width="80"> </el-table-column>
        <el-table-column prop="id" label="学号" width="130"> </el-table-column>
        <el-table-column prop="seat" label="座位号" width="80">
        </el-table-column>
        <el-table-column prop="classroomName" label="教室"> </el-table-column>
      </el-table>
      <div flex-box="1" style="margin-left: 30px">
        <div flex>
          <div
            style="
            background-color: #0099FF;
            height: 70px;
            width: 500px
            text-align: center;
            border-radius:10px;
            color:white;
            margin-right:30px
          "
          >
            <div
              style="margin-bottom: 10px; padding-top: 10px; padding-left: 10px"
            >
              考试科目：{{ examInfo.name }}
            </div>
            <div style="padding-left: 10px">
              考试起止时间：{{ timeTxt(examInfo.startTime, examInfo.endTime) }}
            </div>
          </div>
          <div
            style="
            background-color: #0099FF;
            height: 70px;
            width: 500px
            text-align: center;
            border-radius:10px;
            color:white
          "
          flex
          >
            <div>
              <img src="./assets/notice.png" class="img-class" alt="" />
            </div>
            <span style="margin-top:15px;margin-left:30px;font-size:24px">诚信考试  杜绝作弊 </span>
          </div>
        </div>
        <div
          style="
            background-color: #1c86ee;
            height: 50px;
            text-align: center;
            letter-spacing: 100px;
          "
        >
          <h1 style="color: white">座位表</h1>
        </div>
        <div style="margin-left: 150px">
          <div v-for="(item, index) in classrooms" :key="index">
            <div
              class="rectangle"
              style="
                margin-top: 10px;
                margin-left: 250px;
                background-color: green;
                display: block;
              "
            >
              {{ item.name }}
            </div>
            <div
              class="rectangle"
              style="margin-top: 10px; margin-left: 250px; display: block"
            >
              讲台
            </div>
            <span>监考老师：{{item.teacher.name}}</span>
            <div style="margin:10px"></div>
            <div v-for="i in parseInt(item.numExam / 8 + 1)" :key="i">
              <span class="rectangle" v-for="j in 4" :key="j">
                <span style="float: left; margin-left: 10px">{{
                  parseInt(item.numExam / 8 + 1) * (2 * j - 2) + i
                }}</span>
                <span style="float: right; margin-right: 10px">{{
                  parseInt(item.numExam / 8 + 1) * (2 * j - 1) + i
                }}</span>
              </span>
            </div>
            <el-divider></el-divider>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import {
  GetExamsAndClasses,
  ArrangeStudent,
  ArrangeFirst,
  Search,
} from "./api";
import { DateFormate } from "./utils/DateUtils.js";
export default {
  data() {
    return {
      exams: [],
      classes: [],
      selectedClasses: [],
      students: [],
      arrangeType: "SNO",
      examId: "",
      classNum: "",
      fullscreenLoading: false,
      tableData: [],
      classrooms: [],
      examInfo: "",
      types: [
        {
          id: 1,
          label: "学号乱序",
          value: "SNO",
        },
        {
          id: 2,
          label: "姓名乱序",
          value: "NAME",
        },
      ],
    };
  },
  mounted() {
    this.setInfo();
  },
  watch: {
    async arrangeType(val) {
      this.clean();
      this.fullscreenLoading = true;
      let res = await ArrangeStudent({ arrangeType: val });
      if (res) {
        this.$message({
          message: "排座成功",
          type: "success",
        });
      } else {
        this.$message({
          message: "排座失败",
          type: "error",
        });
      }
      this.fullscreenLoading = false;
      this.setInfo();
    },
    examId(val) {
      this.classNum = "";
      this.selectedClasses = [];
      this.classes.forEach((x) => {
        if (x.examId == val) {
          this.selectedClasses.push(x);
        }
      });
    },
  },

  methods: {
    clean() {
      this.exams = [];
      this.classes = [];
      this.selectedClasses = [];
      this.students = [];
      this.tableData = [];
      this.classrooms = [];
      this.examInfo = {};
      this.examId = "";
      this.classNum = "";
    },
    timeTxt(start, end) {
      let fmt = "YYYY-mm-dd HH:MM";
      return (
        DateFormate(new Date(start), fmt) +
        "~" +
        DateFormate(new Date(end), fmt)
      );
    },
    async arrangeFirst() {
      this.clean();
      this.fullscreenLoading = true;
      let res = await ArrangeFirst({
        arrangeType: this.arrangeType,
      });
      if (res) {
        this.$message({
          message: "安排考场成功",
          type: "success",
        });
      } else {
        this.$message({
          message: "安排考场失败",
          type: "error",
        });
      }
      this.fullscreenLoading = false;
      this.setInfo();
    },
    async setInfo() {
      let res = await GetExamsAndClasses();
      this.exams = res.exams;

      this.classes = res.classes;
    },
    async search() {
      if (!this.examId || !this.classNum || !this.arrangeType) {
        this.$message({
          message: "请先选择必选项",
          type: "error",
        });
        return;
      }
      let res = await Search({
        examId: this.examId,
        classNum: this.classNum,
        arrangeType: this.arrangeType,
      });
      this.examInfo = res;
      this.classrooms = res.classrooms;
      this.tableData = [];
      for (let index = 0; index < this.classrooms.length; index++) {
        let classroomDTO = this.classrooms[index];
        this.tableData = this.tableData.concat(classroomDTO.studentDTOs);
      }
      if (this.arrangeType == "SNO") {
        this.tableData.sort(function (a, b) {
          var x = a.id.toLowerCase();
          var y = b.id.toLowerCase();
          if (x < y) {
            return -1;
          }
          if (x > y) {
            return 1;
          }
          return 0;
        });
      } else {
        this.tableData.sort(function (a, b) {
          var x = a.name.toLowerCase();
          var y = b.name.toLowerCase();
          if (x < y) {
            return -1;
          }
          if (x > y) {
            return 1;
          }
          return 0;
        });
      }
    },
  },
};
</script>
<style lang="scss" scoped>
.rectangle {
  height: 30px;
  width: 120px;
  display: inline-block;
  line-height: 30px;
  text-align: center;
